.header {
  padding-bottom: 37px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

@media screen and (max-width: 560px) {
  .header {
    padding-bottom: 30px;
    flex-wrap: wrap-reverse;
  }
}
